<template>
<v-layout align-start>
  <v-flex>

    <v-data-table
      :headers="headers"
      :items="artigos"
      :search="search"
      sort-by="nome"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
            <v-btn @click="gerarPDF()">
            <v-icon>print</v-icon>
            </v-btn>
          <v-toolbar-title>Artigos</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-text-field class="text-xs-center"  v-model="search"  append-icon="search" label="buscar"  single-line  hide-details>
          </v-text-field>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="primary" dark class="mb-2"  @click="openItem">Novo</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
  
              <v-card-text>
               <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="codigo" label="Cdigo">                                        
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="categoria"
                                        :items="categorias"
                                        label="Categora">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="nome" label="Nome">                                        
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field type="number" v-model="stock" label="Stock">                                        
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="preco_venda" label="preco_venda">                                        
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="descricao" label="Descrio">                                        
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensagem" :key="v" v-text="v">

                                    </div>
                                </v-flex>
                            </v-layout>
                        </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="save">Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="adModal" max-width="290">
                    <v-card>
                        <v-card-title class="headline" v-if="adAcao==1">
                            Activar Item
                        </v-card-title>
                        <v-card-title class="headline" v-if="adAcao==2">
                            Desactivar Item
                        </v-card-title>
                        <v-card-text>
                            Ests a punto de <span v-if="adAcao==1">activar </span>
                            <span v-if="adAcao==2">desactivar </span> el item {{adNome}}
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">
                                Cancelar
                            </v-btn>
                            <v-btn v-if="adAcao=='1'" @click="activar()" color="orange darken-4" flat="flat">
                                Ativar
                            </v-btn>
                            <v-btn v-if="adAcao=='2'" @click="desactivar()" color="orange darken-4" flat="flat">
                                Desativar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>


        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <template  v-if="item.estado=='ativo'">
          <v-icon
              small
              @click="activarDesactivarMostrar(2,item)">
              block
          </v-icon>
        </template>
        <template v-else>
              <v-icon
              small
              @click="activarDesactivarMostrar(1,item)">
              check
        </v-icon>
        </template>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="listar">Atualizar</v-btn>
      </template>
    </v-data-table>
 

  </v-flex>
 </v-layout>
</template>

<script>
import axios from 'axios';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';

export default {
  name: 'App',
  data () {
    return {
        search:'',
        artigos:[],
       dialog: false,
    headers: [
      
                     { text: 'Actions', value: 'actions', sortable: false },
                    { text: 'Cdigo',value: 'codigo', sortable: false},
                    { text: 'Nombre',value: 'nome', sortable: true},
                    { text: 'Categora',value: 'categoria.nome', sortable: true},
                    { text: 'Stock',value: 'stock', sortable: false},
                    { text: 'Preco Venda',value: 'preco_venda', sortable: false},
                    { text: 'Descrio', value: 'descricao', sortable: false },              
                    { text: 'Estado', value: 'estado', sortable: false }
    ],
   
   editedIndex: -1,
                _id:'',
                categoria:'',
                categorias:[],
                codigo: '',
                nome:'',
                stock:0,
                preco_venda:0,
                descricao:'',
                valida:0,
                validaMensagem:[],
                adModal:0,
                adAcao:0,
                adNome:'',
                    adId:''
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'Nuevo registro' : 'Editar registro'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar();
            this.selectCategoria();
        },
        methods: {
            //https://www.npmjs.com/package/jspdf - tutorial
            gerarPDF(){
                
                var columns =[
                    { title: 'Cdigo',dataKey: 'codigo'},
                    { title: 'Nome',dataKey: 'nome'},
                    { title: 'Categoria',dataKey: 'categoria'},
                    { title: 'Stock',dataKey: 'stock'},
                    { title: 'Preo venda',dataKey: 'preco_venda'}
                ];
                var rows =[];
                this.artigos.map(function(x){
                    rows.push(
                        {nome:x.nome,codigo:x.codigo,categoria:x.categoria.nome,stock:x.stock,preco_venda:x.preco_venda}
                    
                    )
                })

                var doc = new jsPDF('p','pt')
                doc.autoTable(columns,rows,{
                    margin:{top:60},
                    addPageContent:function(data){
                        doc.text("Lista de Artigos", 40,30)
                    }
                })
              
                doc.save('Artigos.pdf')
            },
            selectCategoria(){
                let me=this;
                let categoriaArray=[];
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('categoria/list',configuracao).then(function (response){
                    categoriaArray=response.data;
                    categoriaArray.map(function(x){
                        me.categorias.push({text:x.nome, value:x._id});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
            listar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('artigo/list',configuracao).then(function (response){
                    me.artigos=response.data;
                      me.artigos.forEach((item) => {
                  if(item.estado == '1'){
                      item.estado = 'ativo';
                  }
                   if(item.estado == '0'){
                      item.estado = 'inativo';
                  }
                });

                }).catch(function(error){
                    console.log(error);
                });

            },
            limpar(){
                this._id='';
                this.nome='';
                this.codigo='';
                this.stock=0;
                this.nome=0;
                this.descricao='';
                this.valida=0;
                this.validaMensagem=[];
                this.editedIndex=-1;
            },
            validar(){
                this.valida=0;
                this.validaMensagem=[];
                if (!this.categoria){
                    this.validaMensagem.push('Seleccione una categora');
                }
                if (this.codigo.length>64){
                    this.validaMensagem.push('El cdigo no debe tener ms de 64 caracteres');
                }
                if(this.nome.length<1 || this.nome.length>50){
                    this.validaMensagem.push('El nome del artculo debe tener entre 1-50 caracteres.');
                }
                if(this.descricao.length>255){
                    this.validaMensagem.push('La descripcin del artculo no debe tener ms de 255 caracteres.');
                }
                if (this.stock<0){
                    this.validaMensagem.push('Ingrese un stock valido');
                }
                if (this.nome<0){
                    this.validaMensagem.push('Ingrese un precio de venta valido');
                }
                if (this.validaMensagem.length){
                    this.valida=1;
                }
                return this.valida;
            },
            save(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                if (this.validar()){
                    return;
                }
                if (this.editedIndex >-1){
                    //Cdigo para editar
                    axios.put('artigo/update',
                    {
                        '_id':this._id,
                        'categoria':this.categoria,
                        'codigo':this.codigo,
                        'nome':this.nome,
                        'stock':this.stock,
                        'preco_venda':this.preco_venda,
                        'descricao':this.descricao
                    },configuracao)
                    .then(function(response){
                        me.limpar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }else{
                    //Cdigo para guardar
                    axios.post('artigo/add',
                    {
                        'categoria':this.categoria,
                        'codigo':this.codigo,
                        'nome':this.nome,
                        'stock':this.stock,
                        'preco_venda':this.preco_venda,
                        'descricao':this.descricao
                    },configuracao)
                    .then(function(response){
                        me.limpar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }
            },
            
            openItem(){
                this.codigo='';
                this.nome='';
                this.stock='';
                this.nome='';
                this.preco_venda='';
                this.descricao='';
                this.categoria='';
                this.dialog = true;
                this.editedIndex=-1;
            },
            editItem (item) {
                this._id=item._id;
                this.categoria=item.categoria._id;
                this.codigo=item.codigo;
                this.nome=item.nome;
                this.stock=item.stock;
                this.preco_venda=item.preco_venda;
                this.descricao=item.descricao;
                this.dialog = true;
                this.editedIndex=1;
            },
            activarDesactivarMostrar(acao,item){
                this.adModal=1;
                this.adNome=item.nome;
                this.adId=item._id;
                if (acao==1){
                    this.adAcao=1;
                } else if(acao==2){
                    this.adAcao=2;
                } else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                axios.put('artigo/activate',{'_id':this.adId},configuracao)
                    .then(function(response){
                        me.adModal=0;
                        me.adAcao=0;
                        me.adNome='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            desactivar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                axios.put('artigo/desactivate',{'_id':this.adId},configuracao)
                    .then(function(response){
                        me.adModal=0;
                        me.adAcao=0;
                        me.adNome='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            close () {
                this.dialog = false;
            }
        }
    }
</script>
