<template>
  <v-layout align-center justify-center>
    <v-flex xs12 sm8 md6 lg5 xl4>
      <v-card>
        <v-toolbar dark color="blue darken-3">
          <v-toolbar-title>
            Acesso ao Sistema
          </v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-text-field v-model="email"  autofocus color="accent" label="Email" required>
          </v-text-field>
          <v-text-field  v-model="password"
            type="password"
            color="accent"
            label="Password"
            required
          >
          </v-text-field>
          <v-flex class="red--text" v-if="errorM">
                        {{errorM}}
                    </v-flex>
        </v-card-text>
        <v-card-actions class="px-3 pb-3">
          <v-flex text-xs-right>
            <v-btn color="primary"  @click="logar()" >Logar</v-btn>
          </v-flex>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from 'axios';
export default {
    
    data() {
    return {
        email: '',
        password: '',
        password: '',
        errorM: null
        }
    },

    methods: {
        //qdo logar fazer referencia com store js
     logar() {
        axios.post('usuario/login',{email: this.email, password: this.password})
        .then(resposta => {
            return resposta.data;
            })
        .then(data => {
            this.$store.dispatch("guardarToken",data.tokenReturn);
            this.$router.push({ name: 'home' });
            })
        .catch(error => {
           // console.log(error);
            this.errorM=null;
                if (error.response.status==404){
                this.errorM="No existe  usuario , password   incorrectas.";
                } else{
                this.errorM="Ocurreu un error con  servidor.";
                }
            })

       // .then(function (response){
       // console.log(response.data.user);
       // console.log(response.data.tokenReturn);
      //  }).catch(function(error){
       // console.log(error);
       // });


     }
 }


}
</script>