<template>
<v-layout align-start>
  <v-flex>

    <v-data-table
      :headers="headers"
      :items="entradas"
      :search="search"
      sort-by="nome"
      class="elevation-1"
      v-show="verNovo==0"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>entradas</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>

          <v-text-field class="text-xs-center" v-show="verNovo==0"  v-model="search"  append-icon="search" label="buscar"  single-line  hide-details>
          </v-text-field>

          <v-spacer></v-spacer>
              <v-btn color="primary" dark class="mb-2"  v-show="verNovo==0"  @click="mostrarNovo">Novo</v-btn>

          <v-dialog v-model="dialog"  max-width="1000px">
           
            <v-card>
              <v-card-title>
                <span class="headline">Selecione um artigo</span>
              </v-card-title>
  
              <v-card-text>
                        <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm12 md12 lg12 xl12>
                                    <template>
                                      

                                            <v-flex xs12 sm12 md12 lg12 xl12>                                        
                                        <v-text-field v-model="texto" @keyup.enter="listarArtigos()" class="text-xs-center" append-icon="search" 
                                        label="Buscar"></v-text-field>
                                        <template>
                                            <v-data-table
                                                :headers="cabecalhoArtigos"
                                                :items="artigos"
                                                class="elevation-1"
                                            >
                                                <template v-slot:item="props">
                                                    <tr>
                                                <td class="justify-center layout px-0">
                                                    <v-icon
                                                    small
                                                    class="mr-2"
                                                    @click="codGetDetalhes(props.item)"
                                                    >
                                                    add
                                                    </v-icon>                                                    
                                                </td>
                                                <td>{{ props.item.codigo }}</td>
                                                <td>{{ props.item.nome }}</td>
                                                <td>{{ props.item.categoria.nome }}</td>
                                                <td>{{ props.item.stock }}</td>
                                                <td>{{ props.item.preco_venda }}</td>
                                                <td>{{ props.item.descricao }}</td>
                                                <td>
                                                    <div v-if="props.item.estado">
                                                        <span class="blue--text">Activo</span>
                                                    </div>
                                                    <div v-else>
                                                        <span class="red--text">Inactivo</span>
                                                    </div>
                                                </td>  
                                                </tr>              
                                                </template>
                                            </v-data-table>
                                        </template>
                                    </v-flex>


                                      
                                    </template>
                                </v-flex>
                            </v-layout>
                        </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="adModal" max-width="290">
                    <v-card>
                        <v-card-title class="headline" v-if="adAcao==1">
                            Activar Item
                        </v-card-title>
                        <v-card-title class="headline" v-if="adAcao==2">
                            Desactivar Item
                        </v-card-title>
                        <v-card-text>
                            Ests a punto de <span v-if="adAcao==1">activar </span>
                            <span v-if="adAcao==2">desactivar </span> el item {{adNome}}
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">
                                Cancelar
                            </v-btn>
                            <v-btn v-if="adAcao=='1'" @click="activar()" color="orange darken-4" flat="flat">
                                Ativar
                            </v-btn>
                            <v-btn v-if="adAcao=='2'" @click="desactivar()" color="orange darken-4" flat="flat">
                                Desativar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>


        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="verEntrada(item)">
        tab
      </v-icon>
        <template  v-if="item.estado=='ativo'">
          <v-icon
              small
              @click="activarDesactivarMostrar(2,item)">
              block
          </v-icon>
        </template>
        <template v-else>
              <v-icon
              small
              @click="activarDesactivarMostrar(1,item)">
              check
        </v-icon>
        </template>
      </template>
     
    </v-data-table>
 
        <v-container grid-list-md v-show="verNovo">
            <v-layout wrap>
                 <v-flex x12 sm4 md4 lg4 xl4>
                    <v-select  :items="comprovantes" v-model="tipo_comprovante"  label="Tipo Comprovante"></v-select>
                </v-flex>
                <v-flex x12 sm4 md4 lg4 xl4>
                    <v-text-field v-model="serie_comprovante"  label="Serie Comprovante"></v-text-field>
                </v-flex>
                 <v-flex x12 sm4 md4 lg4 xl4>
                    <v-text-field v-model="num_comprovante"  label="Numero Comprovante"></v-text-field>
                </v-flex>
                  <v-flex x12 sm4 md4 lg8 xl8>
                    <v-autocomplete  :items="pessoas" v-model="pessoa"  label="Fornecedor"></v-autocomplete>
                </v-flex>
                <v-flex x12 sm4 md4 lg4 xl4>
                  <v-text-field  type="number" v-model="imposto"  label="Imposto"></v-text-field>
                </v-flex>
                 <v-flex x12 sm8 md8 lg8 x8>
                  <v-text-field   v-model="codigo"  label="Cdigo"  @keyup.enter="buscarCodigo" ></v-text-field>
                </v-flex>
                <v-flex x12 sm2 md2 lg2 xl2>
                  <v-btn small fab dark color="teal" @click="mostarModalArtigos()" >
                      <v-icon  dark >list</v-icon>
                  </v-btn>
                </v-flex>
                <v-flex x12 sm2 md2 lg2 xl2 v-show="errorArtigo" >
                  <div class="red--text"  v-text="errorArtigo"></div>
                </v-flex>
                 <v-flex x12 sm12 md12 lg12 xl12>
                     <template>
                     <v-data-table
                         :headers="cabecalhoDetalhes"
                         :items="detalhes"
                         hide-default-footer
                         class="elevation-1"
                     >
                          <template  slot="item" slot-scope="props" >
                              <tr>
                                <td>
                                    <v-icon  @click="artigoDeleteDetalhes(detalhes,props.item)" small class="mr-2" >delete</v-icon>
                                </td>  
                                <td class="text-xs-center">{{props.item.artigo}}</td> 
                                <td class="text-xs-center"><v-text-field  v-model="props.item.qtde" type="number" ></v-text-field></td> 
                                <td class="text-xs-center"><v-text-field  v-model="props.item.preco"  ></v-text-field></td> 
                                <td class="text-xs-center">${{props.item.qtde * props.item.preco }}</td> 
                              </tr>
                          </template>
                          <template  slot="no-data" >
                              <h3>Nao existe artigos em detalhes</h3>
                          </template>
                     </v-data-table>  
                        <v-flex class="text-xs-right">
                            <strong>Total Parcial:</strong>$ {{totalParcial = (total - totalImposto).toFixed(2)}}
                        </v-flex>
                        <v-flex class="text-xs-right">
                            <strong>Total Imposto:</strong>$ {{totalImposto = ((total * imposto)/(1 + imposto)).toFixed(2)}}
                        </v-flex>
                        <v-flex  class="text-xs-right">
                            <strong>Total :</strong>$ {{total = calcularTotal}}
                        </v-flex>

                      </template> 
                 </v-flex>
                  <v-flex xs12 sm12 md12 lg12 xl12>
                    <div class="red--text" v-for="v in validaMensagem" :key="v"  v-text="v"  >Cancelar</div>
                </v-flex>
                <v-flex xs12 sm12 md12 lg12 xl12>
                    <v-btn color="blue darken-1"  text @click.native="ocultarNovo()" >Cancelar</v-btn>
                    <v-btn color="success" v-if="verDetalhe==0"  @click.native="save()" >Salvar</v-btn>
                </v-flex>
            </v-layout>
        </v-container>

  </v-flex>
 </v-layout>
</template>

<script>
import axios from 'axios';
export default {
  name: 'App',
  data () {
    return {
             search:'',
             entradas:[],
             dialog: false,
             headers: [
      
                    { text: 'Actions', value: 'actions', sortable: false },
                    { text: 'Usuario', value: 'usuario.nome', sortable: true },
                    { text: 'Pessoa', value: 'pessoa.nome', sortable: true },
                    { text: 'Tipo Documento', value: 'tipo_comprovante', sortable: true },
                    { text: 'Srie comprovante', value: 'serie_comprovante', sortable: false  },
                    { text: 'Nmero comprovante', value: 'num_comprovante', sortable: false  },
                    { text: 'Fecha', value: 'createdAt', sortable: false  },
                    { text: 'Imposto', value: 'imposto', sortable: false  },
                    { text: 'Total', value: 'total', sortable: false  },
                    { text: 'Estado', value: 'estado', sortable: false  } 
                ],
   
                _id:'',
                pessoa:'',
                pessoas:[],
                tipo_comprovante:'',
                comprovantes: ['BOLETO','FATURA','TICKET','GUIA'],
                serie_comprovante: '',
                num_comprovante: '',
                imposto: 0.18,
                codigo:'',
                cabecalhoDetalhes:[
                    { text: 'Apagar', value: 'apagar', sortable: false  },
                    { text: 'Artigo', value: 'artigo', sortable: false  },
                    { text: 'Qtde', value: 'qtde', sortable: false  },
                    { text: 'Preo', value: 'preco', sortable: false  },
                    { text: 'Sub Total', value: 'subtotal', sortable: false  }
                ],
                detalhes:[],
                verNovo:0,
                errorArtigo:null,
                total:0,
                totalParcial:0,
                totalImposto:0,
                artigos:[],
                texto:'',
                cabecalhoArtigos: [
                    { text: 'Selecionar', value: 'selecionar', sortable: false },
                    { text: 'Cdigo',value: 'codigo', sortable: false},
                    { text: 'Nome',value: 'nome', sortable: true},
                    { text: 'Categoria',value: 'categoria.nome', sortable: true},
                    { text: 'Stock',value: 'stock', sortable: false},
                    { text: 'Preco Venda',value: 'preco_venda', sortable: false},
                    { text: 'Descrio', value: 'descricao', sortable: false },              
                    { text: 'Estado', value: 'estado', sortable: false }
                ],
                verDetalhe:0,
                valida: 0,
                validaMensagem:[],
                adModal:0,
                adAcao:0,
                adNome:'',
                adId:''


    } //fim return
  }, //fim data
computed: {
 
            calcularTotal: function(){
                let resultado=0.0;
                for(var i=0;  i < this.detalhes.length;  i++){
                    resultado = resultado+(this.detalhes[i].qtde * this.detalhes[i].preco);
                }
                return resultado;
            }
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
  },

  created () {
     this.listar()
     this.selectPessoa()
  },

  methods: {
           selectPessoa(){
                let me=this;
                let pessoaArray=[];
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('pessoa/listFornecedores',configuracao).then(function (response){
                    pessoaArray=response.data;
                    pessoaArray.map(function(x){
                        me.pessoas.push({text:x.nome, value:x._id});
                    });
                }).catch(function(error){
                    console.log(error);
                });
            },
             buscarCodigo(){
               let me=this;
               me.errorArtigo=null;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('artigo/queryCodigo?codigo='+this.codigo,configuracao).then(function (response){
                   // me.entradas=response.data;
                    me.codGetDetalhes(response.data)
                }).catch(function(error){
                    me.errorArtigo="Cdigo de  artigo esta invlido!"
                    console.log(error);
                });
            },
            //agregarDetalle
            codGetDetalhes(data){
                this.errorArtigo=null;
                if(this.checkIdCod(data._id)==true){
                    this.errorArtigo="artigo ja foi includo!";
                }else{
                   this.detalhes.push(
                       {
                      _id:data._id,
                      artigo:data.nome,
                      qtde:1,
                      preco:data.preco_venda
                       }
                   );
                   this.codigo=''; 
                }
            },
            //nao repetir
            checkIdCod(id){
                 let sw=0;
                for (let i = 0; i < this.detalhes.length; i++) {
                    if(this.detalhes[i]._id==id){
                     sw = true;
                    }
                }
                return sw;
            },

            artigoDeleteDetalhes(arr,item){
                  let i=arr.indexOf(item);
                  //verifica se indice eh valido
                  if(i!= -1){
                    arr.splice(i,1);
                  }
            },

                listarArtigos(){
                     let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('artigo/list?valor='+this.texto,configuracao).then(function (response){
                    me.artigos=response.data;
                      me.artigos.forEach((item) => {
                  if(item.estado == '1'){
                      item.estado = 'ativo';
                  }
                   if(item.estado == '0'){
                      item.estado = 'inativo';
                  }
                });

                }).catch(function(error){
                    console.log(error);
                });

            },

            listarDetalhes(id){
               let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('entrada/query?_id='+id,configuracao).then(function (response){
                    //--------SOMENTE DETALHES-------
                    me.detalhes=response.data.detalhes;

                }).catch(function(error){
                    console.log(error);
                });
            },

            verEntrada(item){
                this.limpar();
                this.tipo_comprovante=item.tipo_comprovante;
                this.serie_comprovante=item.serie_comprovante;
                this.num_comprovante=item.num_comprovante;
                this.pessoa=item.pessoa._id;
                this.imposto=item.imposto;
                this.listarDetalhes(item._id);
                this.verNovo=1;
                this.verDetalhe=1;
              // Console.log(item_id)
            },

            listar(){
               let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('entrada/list',configuracao).then(function (response){
                    me.entradas=response.data;

                      me.entradas.forEach((item) => {
                  if(item.estado == '1'){
                      item.estado = 'ativo';
                  }
                   if(item.estado == '0'){
                      item.estado = 'Anulado';
                  }
                });

                }).catch(function(error){
                    console.log(error);
                });
            },


    mostrarNovo(){
        this.verNovo = 1;
    },
    ocultarNovo(){
        this.verNovo = 0;
        this.limpar();
    },
    mostarModalArtigos(){
        this.dialog = 1;
        
    },



   // deleteItem (item) {
    //  const index = this.desserts.indexOf(item)
    //  confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
    //},

    close () {
      this.dialog = false;
    },

      limpar(){
                this._id='';
                this.tipo_comprovante='';
                this.serie_comprovante='';
                this.num_comprovante='';
                this.imposto=0.18;
                this.codigo='';
                this.total=0;
                this.totalParcial=0;
                this.totalImposto=0;
                this.detalhes=[];
                this.verNovo=0;
                this.valida=0;
                this.validaMensagem=[];
                this.verDetalhe=0;
      },

        validar(){
                this.valida=0;
                this.validaMensagem=[];
                if(!this.persona){
                    this.validaMensagem.push('Seleccione un proveedor.');
                }
                if(!this.tipo_comprovante){
                    this.validaMensagem.push('Seleccione un tipo de comprovante.');
                }
                if(!this.num_comprovante){
                    this.validaMensagem.push('Ingrese el nmero del comprovante.');
                }
                if(!this.imposto || this.imposto<0 || this.imposto>1){
                    this.validaMensagem.push('Ingrese un imposto vlido.');
                }
                if(this.detalhes.length<=0){
                    this.validaMensagem.push('Ingrese al menos un artculo al detalle');
                }
                if (this.validaMensagem.length){
                    this.valida=1;
                }
                return this.valida;
            },

      save () {
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                if (this.validar()){
                  
             
                    //Cdigo para salvar
                    axios.post('entrada/add',
                    {
                       'pessoa':this.pessoa,
                    'usuario':this.$store.state.usuario._id,
                    'tipo_comprovante': this.tipo_comprovante,
                    'serie_comprovante': this.serie_comprovante,
                    'num_comprovante':this.num_comprovante,
                    'imposto': this.imposto,
                    'total':this.total,
                    'detalhes':this.detalhes
                    },configuracao)
                    .then(function(response){
                        me.limpar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }
    },


      activarDesactivarMostrar(acao,item){

                 this.adModal=1;
                this.adNome=item.num_comprovante;
                this.adId=item._id;
                if (acao==1){
                    this.adAcao=1;
                } else if(acao==2){
                    this.adAcao=2;
                } else{
                    this.adModal=0;
                }
               
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
               let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                axios.put('entrada/activate',{'_id':this.adId},configuracao)
                    .then(function(response){
                        me.adModal=0;
                        me.adAcao=0;
                        me.adNome='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            desactivar(){
               let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                axios.put('entrada/desactivate',{'_id':this.adId},configuracao)
                    .then(function(response){
                        me.adModal=0;
                        me.adAcao=0;
                        me.adNome='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            close () {
                this.dialog = false;
            } 


  },


};
</script>
