import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Categoria from '../components/Categoria.vue'
import Usuario from '../components/Usuario.vue'
import Login from '../components/Login.vue'
import store from '../store/index'
import Artigo from '../components/Artigo.vue'
import Cliente from '../components/Cliente.vue'
import Entrada from '../components/Entrada.vue'
import Venda from '../components/Venda.vue'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta:{
      administrador:true,
      supervisor:true,
      vendedor:true
    }
  },
  {
    path: '/categoria',
    name: 'categoria',
    component: Categoria,
    meta:{
      administrador:true,
      supervisor:true
    }
  },
  {
    path: '/artigo',
    name: 'artigo',
    component: Artigo,
    meta:{
      administrador:true,
      supervisor:true
    }
  },
  {
    path: '/cliente',
    name: 'cliente',
    component: Cliente,
    meta:{
      administrador:true,
      vendedor:true
    }
  },
  {
    path: '/entrada',
    name: 'entrada',
    component: Entrada,
    meta:{
      administrador:true,
      vendedor:true
    }
  },
  {
    path: '/venda',
    name: 'venda',
    component: Venda,
    meta:{
      administrador:true,
      vendedor:true
    }
  },
  {
    path: '/usuario',
    name: 'usuario',
    component: Usuario,
    meta:{
      administrador:true
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta:{
      livre:true
    }
  },
   
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

//store.js verifica permissao por rota
router.beforeEach((to, from, next) => {
  //console.log(to);
    if (to.matched.some(record => record.meta.livre)){
    next()
    }else if (store.state.usuario && store.state.usuario.rol == 'Administrador') {
    if (to.matched.some(record => record.meta.administrador)) {
      next()
    }
    }else if (store.state.usuario && store.state.usuario.rol == 'Supervisor') {
    if (to.matched.some(record => record.meta.supervisor)) {
    next()
    }
    } else if (store.state.usuario && store.state.usuario.rol == 'Vendedor') {
    if (to.matched.some(record => record.meta.vendedor)) {
    next()
    }
    } else {
    next({
    name: 'login'
    })
    }
 })
 

export default router
