<template>
<v-layout align-start>
  <v-flex>

    <v-data-table
      :headers="headers"
      :items="pessoas"
      :search="search"
      sort-by="nome"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Categorias</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
          <v-text-field class="text-xs-center"  v-model="search"  append-icon="search" label="buscar"  single-line  hide-details>
          </v-text-field>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on }">
              <v-btn color="primary" dark class="mb-2"  @click="openItem">Novo</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="headline">{{ formTitle }}</span>
              </v-card-title>
  
              <v-card-text>
               <v-container grid-list-md>
                            <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                    <v-text-field v-model="nome" label="Nome"></v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-select v-model="tipo_documento"
                                    :items="documentos" label="Tipo Documento">
                                    </v-select>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="num_documento" label="Nmero Documento">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="direcao" label="Direo">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="telefone" label="Telefone">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm6 md6>
                                    <v-text-field v-model="email" label="Email">
                                    </v-text-field>
                                </v-flex>
                                <v-flex xs12 sm12 md12 v-show="valida">
                                    <div class="red--text" v-for="v in validaMensagem" :key="v" v-text="v">

                                    </div>
                                </v-flex>
                            </v-layout>
                        </v-container>
              </v-card-text>
  
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="save">Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="adModal" max-width="290">
                    <v-card>
                        <v-card-title class="headline" v-if="adAcao==1">
                            Activar Item
                        </v-card-title>
                        <v-card-title class="headline" v-if="adAcao==2">
                            Desactivar Item
                        </v-card-title>
                        <v-card-text>
                            Ests a punto de <span v-if="adAcao==1">activar </span>
                            <span v-if="adAcao==2">desactivar </span> el item {{adNome}}
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn @click="activarDesactivarCerrar()" color="green darken-1" flat="flat">
                                Cancelar
                            </v-btn>
                            <v-btn v-if="adAcao=='1'" @click="activar()" color="orange darken-4" flat="flat">
                                Ativar
                            </v-btn>
                            <v-btn v-if="adAcao=='2'" @click="desactivar()" color="orange darken-4" flat="flat">
                                Desativar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>


        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <template  v-if="item.estado=='ativo'">
          <v-icon
              small
              @click="activarDesactivarMostrar(2,item)">
              block
          </v-icon>
        </template>
        <template v-else>
              <v-icon
              small
              @click="activarDesactivarMostrar(1,item)">
              check
        </v-icon>
        </template>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="listar">Atualizar</v-btn>
      </template>
    </v-data-table>
 

  </v-flex>
 </v-layout>
</template>


<script>
    import axios from 'axios'
    export default {
        data(){
            return{
                dialog: false,
                search:'',
                pessoas:[],
                headers: [
                    { text: 'Actions', value: 'actions', sortable: false },
                    { text: 'Nome', value: 'nome', sortable: true },
                    { text: 'Tipo Persona', value: 'tipo_pessoa', sortable: true },
                    { text: 'Tipo Documento', value: 'tipo_documento', sortable: true },
                    { text: 'Nmero Documento', value: 'num_documento', sortable: false  },
                    { text: 'Direccin', value: 'direcao', sortable: false  },
                    { text: 'Telfono', value: 'telefone', sortable: false  },
                    { text: 'Email', value: 'email', sortable: false  },
                    { text: 'Estado', value: 'estado', sortable: false  } 
                ],
                editedIndex: -1,
                _id:'',
                nome:'',
                tipo_pessoa:'Cliente',
                tipo_documento:'',
                documentos: ['DNI','RUC','PASAPORTE','CEDULA'],
                num_documento: '',
                direcao: '',
                telefone: '',
                email: '',
                valida:0,
                validaMensagem:[],
                adModal:0,
                adAcao:0,
                adNome:'',
                adId:''
            }
        },
        computed: {
            formTitle () {
            return this.editedIndex === -1 ? 'Nuevo registro' : 'Editar registro'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            this.listar()
        },
        methods: {
            listar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};            
                axios.get('pessoa/listClientes',configuracao).then(function (response){
                    me.pessoas=response.data;

                      me.pessoas.forEach((item) => {
                  if(item.estado == '1'){
                      item.estado = 'ativo';
                  }
                   if(item.estado == '0'){
                      item.estado = 'inativo';
                  }
                });

                }).catch(function(error){
                    console.log(error);
                });

            },
            limpar(){
                this._id='';
                this.nome='';
                this.num_documento='';
                this.direcao='';
                this.telefone='';
                this.email='';
                this.valida=0;
                this.validaMensagem=[];
                this.editedIndex=-1;
            },
            validar(){
                this.valida=0;
                this.validaMensagem=[];
                if(this.nome.length<1 || this.nome.length>50){
                    this.validaMensagem.push('El nome de la pessoa debe tener entre 1-50 caracteres.');
                }
                if(this.num_documento.length>20){
                    this.validaMensagem.push('El documento no debe tener ms de 20 caracteres.');
                }
                if(this.direcao.length>70){
                    this.validaMensagem.push('La direccin no debe tener ms de 70 caracteres.');
                }
                if(this.telefone.length>20){
                    this.validaMensagem.push('El telfono no debe tener ms de 20 caracteres.');
                }
                if(this.nome.length>50){
                    this.validaMensagem.push('El email del usuario debe tener menos de 50 caracteres.');
                }
                if (this.validaMensagem.length){
                    this.valida=1;
                }
                return this.valida;
            },
            save(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                if (this.validar()){
                    return;
                }
                if (this.editedIndex >-1){
                    //Cdigo para editar
                    axios.put('pessoa/update',{
                        '_id':this._id,
                        'tipo_pessoa':this.tipo_pessoa,
                        'nome':this.nome,
                        'tipo_documento':this.tipo_documento,
                        'num_documento':this.num_documento,
                        'direcao':this.direcao,
                        'telefone': this.telefone,
                        'email':this.email
                    },configuracao)
                    .then(function(response){
                        me.limpar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }else{
                    //Cdigo para save
                    axios.post('pessoa/add',
                    {
                        'tipo_pessoa':this.tipo_pessoa,
                        'nome':this.nome,
                        'tipo_documento':this.tipo_documento,
                        'num_documento':this.num_documento,
                        'direcao':this.direcao,
                        'telefone': this.telefone,
                        'email':this.email
                    },configuracao)
                    .then(function(response){
                        me.limpar();
                        me.close();
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
                }
            },
              openItem(){
                this.rol='';
                this.nome='';
                this.tipo_documento='';
                this.num_documento='';
                this.direcao='';
                this.telefone='';
                this.email='';
                this.password='';
                this.dialog = true;
                this.editedIndex=-1;
            },
            editItem (item) {
                this._id=item._id;
                this.rol=item.rol;
                this.nome=item.nome;
                this.tipo_documento=item.tipo_documento;
                this.num_documento=item.num_documento;
                this.direcao=item.direcao;
                this.telefone=item.telefone;
                this.email=item.email;
                this.password=item.password;
                this.dialog = true;
                this.editedIndex=1;
            },
            activarDesactivarMostrar(acao,item){
                this.adModal=1;
                this.adNome=item.nome;
                this.adId=item._id;
                if (acao==1){
                    this.adAcao=1;
                } else if(acao==2){
                    this.adAcao=2;
                } else{
                    this.adModal=0;
                }
            },
            activarDesactivarCerrar(){
                this.adModal=0;
            },
            activar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                axios.put('pessoa/activate',{'_id':this.adId},configuracao)
                    .then(function(response){
                        me.adModal=0;
                        me.adAcao=0;
                        me.adNome='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            desactivar(){
                let me=this;
                let header={"Token" : this.$store.state.token};
                let configuracao= {headers : header};
                axios.put('pessoa/desactivate',{'_id':this.adId},configuracao)
                    .then(function(response){
                        me.adModal=0;
                        me.adAcao=0;
                        me.adNome='';
                        me.adId='';
                        me.listar();
                    })
                    .catch(function(error){
                        console.log(error);
                    });
            },
            close () {
                this.dialog = false;
            }
        }
    }
</script>
